export const loader = async ({ params }: LoaderFunctionArgs) => {
  try {
    // setIsLoading(true)
    const myresponse = await getPostByName((params.postId as string));
    const response = await getPostsMeta();
    // setIsLoading(false)

    return json({ post: myresponse!, postsMeta: response, loading: false });
  } catch (error) {
    console.log(error);
  }
};

type UseforPost = {
  post: BlogPost;
  postsMeta: ReletedMetaFDB[];
  loading: boolean;
};

