export default function posts() {
  const params = useParams();
  const { postId } = params;
  const { postsMeta, setPostsMeta } = useContext(postsMetaContext);
  const [post, setPost] = useState<BlogPost | null>(null);
  // const alert = useAlert()

  let myloaderdata = useLoaderData<typeof loader>();
  console.log("myloaderdata=>>>>>>>>> 📲📲📲📲📲📲📲📲", myloaderdata.post);

  useEffect(() => {
    setPost((myloaderdata.post as BlogPost));
    setPostsMeta((myloaderdata.postsMeta as ReletedMetaFDB[]));
  }, [postId]);

  if (!post) {
    return (
      <>
        <PageFormate>
          <div className="w-full max-w-2xl mx-auto pt-[1rem]">
            <div className="w-full rounded my-4">
              <Skeleton height={64 * 4} width="100%" className="rounded" />

              <p>
                <Skeleton count={3} />
              </p>
              <p className="w-[70%] mb-2">
                <Skeleton count={1} />
              </p>
              <p>
                <Skeleton count={4} />
              </p>
              <p className="w-[70%] mb-2">
                <Skeleton count={1} />
              </p>
              <p>
                <Skeleton count={5} />
              </p>
              <p className="w-[70%] mb-2">
                {" "}
                <Skeleton count={1} />
              </p>
            </div>
          </div>
        </PageFormate>
      </>);

  }

  const { meta, content } = (post as BlogPost);
  console.log("tags=***>>>>>", meta.tags);
  const myTags = post.meta.tags;
  console.log("myTags =>>>>>>> ❌❌❌❌❌❌", myTags);
  const TagsPosts: ReletedMetaFDB[] = [];
  if (!postsMeta) {
    return <h1>there are no Posts</h1>;
  }

  myTags.forEach((tag) => {
    const tagPosts = postsMeta.filter((post) => post.tags?.includes(tag) ?? []);

    if (tagPosts.length > 0) {
      tagPosts.forEach((tagPost) => {
        TagsPosts.push(tagPost);
      });
    }
  });

  const notify = (text: string) => {
    toast.success(text, {
      position: "top-right",
      autoClose: 3000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined
    });
  };

  const handleCodeClick = async (
  event: React.MouseEvent<HTMLElement, MouseEvent>) =>
  {
    const codeElement = event.currentTarget;
    const codeText = codeElement.textContent || "";

    try {
      await navigator.clipboard.writeText(codeText);
      notify("Code successfully copied to clipboard");
    } catch (error) {
      console.error("Unable to copy to clipboard", error);
      notify("Failed to copy code to clipboard");
    }
  };

  // Attach the click event to all code elements
  const codeElements = document.getElementsByTagName("pre");

  for (let i = 0; i < codeElements.length; i++) {
    const codeElement = codeElements[i];
    codeElement.style.cursor = "pointer";
    // @ts-ignore: Ignore TypeScript error for event listener
    codeElement.addEventListener("click", (event) => {
      event.stopImmediatePropagation();
      handleCodeClick((
        (event as unknown) as React.MouseEvent<HTMLElement, MouseEvent>)
      );
    });
  }

  return (
    <>
      <MyNavbar></MyNavbar>
      <div className="mainPostContainer pt-[1rem] block px-3  prose prose-2xl prose-pr:m-12 prose-code:border-blue-700  mx-auto">
        <div>
          <h2 className="mt-4 mb-0 text-[1.5rem] dark:text-[ghostwhite] text-gray-900 sm:text-[2rem] lg:text-[3rem] ">
            {meta.title}
          </h2>

          {/* <button className="btn" onClick={() => notify("hello")} >open toast</button> */}

          <p
            style={{ fontSize: "16px" }}
            className="mt-0 text-sm font-[ubuntu] font-[500]">

            {meta.modified.split(" ")[0]}
            <span className="underline ml-1 text-white font-[ubuntu] font-[400]">
              By Shivam.
            </span>
            <span className="text-sm ml-1 font-[400]">
              {" "}
              , 📖 {meta.readTime} min Read
            </span>
          </p>
        </div>
          <article
          className=" myPostContentInArticle
          prose-blockquote:bg-accent/20 
          prose-blockquote:p-2
          prose-blockquote:px-6
          ]
          prose-blockquote:border-accent
          prose-blockquote:not-italic
          prose-blockquote:rounded-r-lg

          prose-li:marker:text-accent

          dark:prose-invert
          dark:prose-blockquote:border-accentDark
          dark:prose-blockquote:bg-accentDark/20
          dark:prose-li:marker:text-accentDark

          first-letter:text-3xl
          sm:first-letter:text-5xl
          ">



















          <div dangerouslySetInnerHTML={{ __html: content }} />
        </article>
      </div>

      <div>
        <Related props={{ MyPostsByTags: TagsPosts }} />
      </div>
      <div className="afterPage">
        <AfterPage></AfterPage>
        <Footer></Footer>
      </div>
    </>);

}